<!-- 机构维护 -->
<template>
  <div class="maintain-detail-box flex-column">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isCustom='false'></Breadcrumb>
    </div>
    <!-- 表单 -->
    <div class="inner-box flex-overflow">
       <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="160px" class="maintainFrom">
         <!-- 机构参数维护 -->
         <div class="item-box">
          <h3 class="title">机构参数维护</h3>
          <el-row>
            <el-col :span="8">
              <el-form-item label="辖区内常驻人口：" prop="areaLivePeople">
                <el-input v-model.number="ruleForm.areaLivePeople" placeholder="请输入" clearable :maxlength="NUMBERLEN"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="非辖区内常驻人口：" prop="inareaLivePeople">
                <el-input v-model.number="ruleForm.inareaLivePeople" placeholder="请输入" clearable :maxlength="NUMBERLEN"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="总居民数：" prop="totalLivePeople">
                <el-input v-model.number="ruleForm.totalLivePeople" placeholder="请输入" clearable :maxlength="NUMBERLEN"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="目标签约人数：" prop="totalTarget">
                <el-input v-model.number="ruleForm.totalTarget" placeholder="请输入" clearable :maxlength="NUMBERLEN"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="目标签约率：" prop="targetSignRate">
                <el-input v-model.number="ruleForm.targetSignRate" placeholder="请输入" clearable :maxlength="NUMBERLEN"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="限制签约人数：" prop="limitTotalTaget">
                <el-input v-model.number="ruleForm.limitTotalTaget" placeholder="请输入" clearable :maxlength="NUMBERLEN"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="目标履约率：" prop="targetAppointRate">
                <el-input v-model.number="ruleForm.targetAppointRate" placeholder="请输入" clearable :maxlength="NUMBERLEN"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
         </div>
         <!-- 服务包限制签约上限 -->
          <div class="item-box">
          <h3 class="title">服务包限制签约上限</h3>
          <el-row>
            <el-col :span="12" v-for="(item,index) in ruleForm.packageVins" :key="item.packageId">
              <el-form-item :label="item.packageName+'：'" class="flex">
                <el-radio-group v-model="item.limitSignStatus">
                  <el-radio :label="1">是</el-radio>
                  <el-radio :label="2">否</el-radio>
                </el-radio-group>
                <el-form-item :prop="'packageVins.' + index +'.limitSignPeople'" :rules="rules.limitSignPeople" v-if="item.limitSignStatus==1">
                  <el-input v-model.number="item.limitSignPeople" placeholder="请输入服务包限制签约上限" clearable class="input-box" :maxlength="NUMBERLEN" ></el-input>
                </el-form-item>
              </el-form-item>
            </el-col>
          </el-row>
         </div>
        <!-- 服务包目标签约人数设置 -->
         <div class="item-box">
          <h3 class="title">服务包目标签约人数设置</h3>
          <el-row>
            <el-col :span="8" v-for="(item,index) in ruleForm.packageVins" :key="item.packageId" :class="index%2===0?'ts-col':''">
              <el-form-item :label="item.packageName+'：'" :prop="'packageVins.' + index +'.packageTarget'" :rules="rules.packageTarget">
                <el-input v-model.number="item.packageTarget" placeholder="请输入" clearable  :maxlength="NUMBERLEN"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
         </div>
        <!-- 人群目标签约人数设置 -->
         <div class="item-box">
          <h3 class="title">人群目标签约人数设置</h3>
          <el-row>
            <el-col :span="8" v-for="(item,index) in ruleForm.labelVins" :key="item.labelId" :class="index%2===0?'ts-col':''">
              <el-form-item :label="item.labelName+'：'" :prop="'labelVins.' + index +'.labelTarget'" :rules="rules.labelTarget">
                <el-input v-model.number="item.labelTarget" placeholder="请输入" clearable :maxlength="NUMBERLEN"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
         </div>
         <!-- 操作 -->
         <div class="btn-box">
            <el-button @click="handleBackList()">取 消</el-button>
            <el-button type="primary" @click="submitForm('ruleForm')">确 认</el-button>
          </div>
       </el-form>
    </div>
  </div>
</template>

<script>
import formRule from '../../../util/formRule'
export default {
    name:'organ-maintain',
    data() {
      return {
        ruleForm:{
          keyId:'',//维护对象编号（机构编号，团队编号，人员编号）
          keyName:'',//维护对象名称
          areaLivePeople:'',//辖区内常驻人口
          inareaLivePeople:'',//非辖区内常驻人口
          totalLivePeople:'',//总居民数
          totalTarget:'',//目标签约人数
          targetSignRate:'',//目标签约率
          limitTotalTaget:'',//限制签约人数
          targetAppointRate:'',//目标履约率
          packageVins:[],//签约包集合
          labelVins:[],//目标人群集合
        },
        rules:{
          areaLivePeople: [
            { validator:formRule.validateInteger, trigger: 'change' }
          ],
          inareaLivePeople: [
            { validator:formRule.validateInteger, trigger: 'change' }
          ],
          totalLivePeople: [
            { validator:formRule.validateInteger, trigger: 'change' }
          ],
          totalTarget: [
            { validator:formRule.validateInteger, trigger: 'change' }
          ],
          targetSignRate: [
            { validator:formRule.validatePercent, trigger: 'change' }
          ],
          limitTotalTaget: [
            { validator:formRule.validateInteger, trigger: 'change' }
          ],
          targetAppointRate: [
            { validator:formRule.validatePercent, trigger: 'change' }
          ],
          packageTarget: [
            {validator:formRule.validateInteger, trigger: 'change' }
          ],
          limitSignPeople: [
            { required: true, message: '请输入服务包限制签约上限', trigger: 'change' },
            {validator:formRule.validateInteger, trigger: 'change' }
          ],
          labelTarget: [
            {validator:formRule.validateInteger, trigger: 'change' }
          ]
        }
      };
    },
    created() {
      this.getMaintainDetail() //获取签约维护详情
    },
    methods: {
      //获取签约维护详情
      getMaintainDetail(){
        this.$http.get(process.env.VUE_APP_API+'/family/vin/getVinDtoByKeyId',{
          keyId:this.$route.query.id,
          keyType:1
        }).then(res => {
          if(res.code===200){
            let result = res.data
            for(let key in this.ruleForm){
              this.ruleForm[key] = result[key]
            }
          }else{
            this.$message.error(res.msg);
          }
        })
        .catch(error => {
          this.$message.error(error);
        });
      },
      //保存设置
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
             let params = {}
             let result = JSON.parse(JSON.stringify(this.ruleForm))
             result.packageVins.forEach(item=>{
               item.limitSignPeople = item.limitSignStatus===1?item.limitSignPeople:''
             })
             params.packageVinsJson = JSON.stringify(result.packageVins)
             params.labelVinsJson = JSON.stringify(result.labelVins)
             params.keyType = 1
             delete this.ruleForm.packageVins
             delete this.ruleForm.labelVins
             params = Object.assign({},this.ruleForm,params)
             this.$http.post(process.env.VUE_APP_API+'/family/vin/updateVinDto',params).then(res => {
                if(res.code===200){
                  this.$message.success('修改成功');
                  this.handleBackList() //返回列表
                }else{
                  this.$message.error(res.msg);
                }
              })
              .catch(error => {
                this.$message.error(error);
              });
          }
        });
      },
      //返回列表
      handleBackList() {
        this.$router.push({
          path: '/basicData-manage/organs-manage/list'
        })
      }
    }
};
</script>

<style lang="scss">
@import "~@/assets/styles/maintain";
</style>
